
.vddl-placeholder {
  width: 100%;
  min-height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  padding: 0 15px;
  background: #f5f5f5;
}
/* new-elements */
.new-elements .button {
  border: 1px solid #42B983;
  text-align: center;
  height: 40px;
  border-radius: 4px;
  line-height: 40px;
  background-color: #42B983;
  color: #fff;
  cursor: move;
  font-size: 14px;
}
.new-elements .vddl-dragging-source {
  display: block;
}
.disable-element .button {
  background-color: #E32822;
  border: 1px solid #E32822;
  cursor: pointer;
}
.selected {
  background: #f9f9f9;
}
.selected-item .panel__body {
  line-height: 40px;
}
.ashcan .ashcan-logo {
  display: block;
  width: 40px;
  margin: 10px auto;
}
.ashcan .vddl-placeholder {
  display: none;
}



