
@font-face {
  font-family: 'icomoon';
  src: url('../fonts/icomoon/icomoon.eot?42zd02');
  src: url('../fonts/icomoon/icomoon.eot?42zd02#iefix') format('embedded-opentype'), url('../fonts/icomoon/icomoon.ttf?42zd02') format('truetype'), url('../fonts/icomoon/icomoon.woff?42zd02') format('woff'), url('../fonts/icomoon/icomoon.svg?42zd02#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-file-text2:before {
  content: "\e926";
}
.icon-folder:before {
  content: "\e92f";
}
.icon-folder-open:before {
  content: "\e930";
}
.icon-play3:before {
  content: "\ea1c";
}
.vddl-tree .vddl-label {
  font-size: 1rem;
  margin-left: 0.3rem;
}
.vddl-tree .vddl-list {
  padding-left: 1.2rem;
  min-height: 4px;
}
.vddl-tree .icon.leaf {
  margin-left: 1.2rem;
}
.vddl-tree .vddl-placeholder {
  height: 1.2rem;
  background: #eee;
}
.icon {
  width: 1.2rem;
  height: 1.2rem;
  display: block;
  float: left;
  text-align: center;
}
.icon i {
  display: inline-block;
  font: normal normal normal 14px/1 icomoon;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon.leaf {
  color: gray;
}
.icon.leaf i:before {
  content: "\e926";
}
.icon.node {
  color: orange;
}
.icon.node i:before {
  content: "\e92f";
}
.icon.node i.open:before {
  content: "\e930";
}
.icon.open-close.open i:before {
  content: "-";
}
.icon.open-close i:before {
  content: "+";
}



