.panel {
  border-radius: 8px;
  border: 1px solid #eee;
  overflow: hidden;
  margin-bottom: 15px;
}
.panel p {
  display: inline-block;
  margin: 0;
}
.panel__heading {
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  background: #f5f5f5;
}
.panel__heading h3 {
  font-size: 15px;
  padding: 0 15px;
  margin: 0;
}
.panel__body {
  background: #fff;
  min-height: 40px;
}
.panel__body > .panel {
  margin: 10px;
}
.panel__body p {
  width: 100%;
  padding: 0 15px;
  font-size: 14px;
  line-height: 20px;
  color: #666;
}
.panel__body--list {
  background: #fff;
}
.panel__body--item,
.panel__body .panel__placeholder {
  width: 100%;
  min-height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  padding: 0 15px;
  background: #ffffff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.panel__body--item.no-padding-left {
  padding-left: 0;
}
.panel__body--item:last-child {
  border-bottom: none;
}
.panel__body--item > .panel {
  margin: 15px 0;
}
.panel .panel__placeholder {
  background: #f5f5f5;
}
.panel.panel--info .panel__body {
  padding: 25px;
}
.main {
  min-height: 350px;
  padding-bottom: 30px;
}



