
.header {
  width: 100%;
  height: 420px;
  border-bottom: 1px solid #f5f5f5;
  margin-bottom: 30px;
  padding-bottom: 50px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.header__container {
  max-width: 980px;
  min-width: 730px;
  height: 100%;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.header__container a {
  margin-bottom: 20px;
}
.header__container .logo {
  color: #fff;
}
.header__container h3 {
  margin: 5px 0;
  font-size: 18px;
  font-weight: normal;
}
.header__container .buttons {
  width: 200px;
  margin: 20px 0;
  text-align: center;
}



